<template>

<div class="app-page">
  <page-header title="测试页面"></page-header>
  <page-content>
<div class="line">
  <div  v-for="data in dataList" :key="data.id">
    <Steps :data="data" :width="50" :height="50">
    </Steps>
    <page-line direction="L" borderStyle="dashed" :length="1" :dashedSpacing="0.1"></page-line>
  </div>
</div>
  </page-content>
</div>
</template>

<script>
import PageHeader from '@/components/page/header'
import PageContent from '@/components/page/content'
import Steps from '@/components/page/step/steps'
import PageLine from '@/components/page/line'
export default {
  name: 'test2',
  components: {PageContent, PageHeader, Steps, PageLine},
  data () {
    return {
      dataList: [
        {id: 1, status: 0},
        {id: 2, status: 1},
        {id: 3, status: 2},
        {id: 4, status: 0},
        {id: 5, status: 1},
        {id: 6, status: 2}
      ]
    }
  },
  methods: {

  },
  mounted () {

  }
}
</script>
<style rel='stylesheet/scss' lang='scss' scoped>
.line{
  display: flex;
  flex-direction: row;
}
</style>



