<template>
<div class="steps" >
  <div class="steps__head is-process">
    <div class="steps__icon is-text" :style="style"><!---->
      <!--<slot name="icon">-->
      <!--</slot>-->
      <img  src="/static/images/commonicon/wxz.png" slot="icon" v-if="data.status == 0"/>
      <img  src="/static/images/commonicon/wxz.png" slot="icon" v-if="data.status == 1"/>
      <img  src="/static/images/commonicon/dqdd.png" slot="icon" v-if="data.status == 2"/>
    </div>
  </div>
</div>
</template>
<script>

export default {
  name: 'steps',
  props: {
    currentStatus: String,
    icon: String,
    data: Object,
    width: {
      Number,
      default: 0
    },
    height: {
      Number,
      default: 0
    }
  },
  computed: {
    style () {
      return {
        width: this.width + 'px',
        height: this.width + 'px'
      }
    }
  }
}
</script>

<style scoped type="text/scss" lang="scss">
.steps{

.steps__head{
  position: relative;
  width: 100%;
  text-align: left;
  .steps__line{
    position: absolute;
    border-color: inherit;
    background-color: #c0c4cc;
    height: 2px;
    top: 11px;
    left: 0;
    right: 0;
    &:last-child {
      color: red;
    }
  }
  .steps__icon{
    .is-icon{
      width: 40px;
    }
    position: relative;
    z-index: 1;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    font-size: 14px;
    box-sizing: border-box;
    background: #fff;
    transition: .15s ease-out;
  }
  img{
    width: 100%;
    height:100%;
  }
}

}
</style>



